body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.cps-maket {
  font-family: 'TT Lakes';
  display: flex;
  min-width: 320px;
  flex-direction: column;
  background-color: #f8f8f8;
  color: #1b1c21;
  margin: 0;
  gap: 32px; }

.footer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #7e7e82;
  padding: 16px; }

@media screen and (min-width: 767px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 24px 24px 24px;
    gap: 0px; }
    .footer > p {
      flex-basis: 100%; }
      .footer > p:first-child {
        text-align: left; }
      .footer > p:last-child {
        text-align: right; } }
