body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.cps-maket {
  font-family: 'TT Lakes';
  display: flex;
  min-width: 320px;
  flex-direction: column;
  background-color: #f8f8f8;
  color: #1b1c21;
  margin: 0;
  gap: 32px; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  -moz-column-gap: 16px;
       column-gap: 16px;
  padding: 16px 20px; }

.button {
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-position-x: right;
  border: 0;
  padding: 0;
  background-color: transparent; }
  .button:hover {
    cursor: pointer;
    opacity: 0.6;
    transition: 0.2s; }
  .button span {
    display: none; }

.header-start {
  display: flex;
  -moz-column-gap: 16px;
       column-gap: 16px;
  align-items: center; }
  .header-start .header__divider {
    display: none; }
  .header-start__button--burger {
    background-image: url(./static/burger.svg); }
  .header-start__button--burger::after {
    margin-left: 62px;
    content: '';
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    display: none; }
    .header-start__button--burger::after__logo {
      width: 97px;
      height: 51px;
      object-fit: contain; }

.header-end {
  display: flex;
  -moz-column-gap: 16px;
       column-gap: 16px;
  align-items: center; }
  .header-end__button--repair {
    background-image: url(./static/repair.svg); }
  .header-end__button--repair::after {
    margin-left: -22px;
    content: '';
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    display: block; }
  .header-end__button--checkstatus {
    background-image: url(./static/checkstatus.svg); }
  .header-end__button--profile {
    background-image: url(./static/profile.svg);
    margin-right: 26px; }
  .header-end__button--call {
    background-image: url(./static/call.svg); }
  .header-end__button--chat {
    background-image: url(./static/chat.svg); }
  .header-end__button--call, .header-end__button--chat, .header-end__button--profile {
    display: none; }

@media (min-width: 768px) {
  .header-start__button--burger,
  .header-end__button--call,
  .header-end__button--chat,
  .header-end__button--profile {
    display: block; }
  .header-start .header-start__button--burger {
    margin-right: 24px; }
  .header-start .header-start__button--burger::after {
    display: block; } }

@media (min-width: 1120px) {
  .header {
    display: none; }
  .header-start,
  .header-end__button--call,
  .header-end__button--chat,
  .header-end__button--profile,
  .header__divider {
    display: none; }
  .header-end__button--repair,
  .header-end__button--checkstatus {
    display: flex;
    align-items: center;
    gap: 8px;
    width: auto;
    padding-right: 48px; }
  .button span {
    display: inline; }
  .header-end__button--repair {
    margin-right: 24px; }
    .header-end__button--repair::after {
      display: none; }
  .header-end__button--checkstatus {
    margin-right: 16px; } }
