body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.cps-maket {
  font-family: 'TT Lakes';
  display: flex;
  min-width: 320px;
  flex-direction: column;
  background-color: #f8f8f8;
  color: #1b1c21;
  margin: 0;
  gap: 32px; }

.main {
  background-color: #f8f8f8;
  padding-top: 24px;
  flex-grow: 1; }
  .main .main-title {
    font-size: 16px;
    letter-spacing: -0.6px;
    margin: 0;
    padding-left: 16px;
    border-left: 4px solid #41f6d7; }
    .main .main-title__button {
      display: none; }
    .main .main-title__text {
      margin: 0;
      font-size: 28px;
      flex-grow: 1; }

.nav-list {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  overflow-x: auto;
  padding: 0px; }
  .nav-list::-webkit-scrollbar {
    display: none; }
  .nav-list__item {
    justify-content: center;
    white-space: nowrap;
    list-style-type: none;
    padding: 8px;
    padding-left: 16px;
    border: 4px solid transparent;
    border-radius: 5%; }
    .nav-list__item:first-child {
      margin-left: 16px; }
    .nav-list__item:last-child {
      margin-right: 16px; }
    .nav-list__item:hover {
      border-color: #b8ffec;
      background-color: #ffffff;
      cursor: pointer; }
  .nav-list__link {
    color: #7e7e82;
    text-decoration: none; }

.main-table {
  display: flex;
  flex-direction: column; }
  .main-table__onwards {
    display: flex;
    align-items: center;
    -moz-column-gap: 8px;
         column-gap: 8px;
    margin-left: 16px;
    margin-bottom: 16px;
    margin-top: 10px;
    background-color: #f8f8f8; }
  .main-table p {
    margin-left: 16px;
    margin-right: 16px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px; }
  .main-table__img {
    object-fit: contain; }
  .main-table__text {
    max-height: 105px;
    overflow: hidden; }
  .main-table__text--open {
    max-height: unset; }

@media (min-width: 768px) {
  .main-table {
    flex-direction: row; }
    .main-table > * {
      width: 50%; }
    .main-table__text {
      max-height: 135px;
      overflow: hidden; }
    .main-table__text--open {
      max-height: unset; } }

@media (min-width: 1120px) {
  .main-title {
    display: flex;
    align-items: center; }
  .button span {
    display: inline; }
  .main-table__text {
    max-height: unset; }
  .main {
    padding-top: 42px; }
  .main .main-title__button {
    display: block; }
  .nav-list {
    flex-wrap: wrap; }
    .nav-list__item:last-child {
      margin-left: 16px; } }
