body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.cps-maket {
  font-family: 'TT Lakes';
  display: flex;
  min-width: 320px;
  flex-direction: column;
  background-color: #f8f8f8;
  color: #1b1c21;
  margin: 0;
  gap: 32px; }

.modal-menu {
  float: left;
  border: none;
  max-width: 320px;
  height: 100vh;
  max-height: 100vh;
  overflow-y: hidden;
  position: relative;
  top: 0;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02); }
  @media (min-width: 1440px) {
    .modal-menu {
      display: block;
      width: 320px; } }

.mobile-menu {
  --menu-padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 40px;
  box-sizing: border-box;
  background-color: #ffffff;
  color: #1b1c21;
  width: 320px;
  height: 100vh;
  margin: 0;
  padding: var(--menu-padding); }

.mobile-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff; }
  .mobile-head__button--close {
    background-image: url(./static/close.svg);
    margin-right: 16px; }
  .mobile-head__logo {
    margin-right: 78px; }
  .mobile-head__button--search {
    background-image: url(./static/search.svg); }

.navigator {
  flex-grow: 1; }

.mobile-list {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  list-style-type: none;
  padding: 0px;
  margin: 0; }
  .mobile-list .mobile-list__item {
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    font-weight: 500;
    position: relative; }
    .mobile-list .mobile-list__item a:hover {
      color: #7e7e82;
      transition: 0.2s; }
    .mobile-list .mobile-list__item::before {
      --item-border-margin: 5px;
      content: '';
      position: absolute;
      left: calc(-1 * var(--menu-padding));
      top: calc(-1 * var(--item-border-margin));
      width: 4px;
      height: calc(100% + (var(--item-border-margin) * 2));
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      background: transparent;
      transition: background 300ms; }
    .mobile-list .mobile-list__item:hover::before {
      background: #41f6d7; }
  .mobile-list__link {
    color: #1b1c21;
    text-decoration: none; }

.mobile-contact {
  display: flex;
  flex-direction: column; }

.contact-buttons {
  display: flex;
  gap: 16px;
  padding: 0;
  margin: 0; }
  .contact-buttons__item {
    list-style-type: none; }
  .contact-buttons__button--call {
    background-image: url(./static/call.svg); }
  .contact-buttons__button--chat {
    background-image: url(./static/chat.svg); }
  .contact-buttons__button--profile {
    background-image: url(./static/profile.svg); }

.mobile-address {
  display: flex;
  flex-direction: column;
  font-style: normal; }
  .mobile-address__mail {
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    font-weight: 600;
    margin-top: 16px;
    color: #1b1c21;
    text-decoration: none; }
  .mobile-address__tel {
    font-size: 24px;
    line-height: 32px;
    font-weight: bold;
    margin-top: 12px;
    color: #1b1c21;
    text-decoration: none; }

.mobile-lang {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 40px 0 0; }
  .mobile-lang__item {
    font-size: 16px;
    margin-right: 7px;
    color: #1b1c21;
    text-decoration: none; }
    .mobile-lang__item:hover {
      color: #7e7e82; }

@media (min-width: 1440px) {
  .mobile-head__button--close {
    display: none; }
  .modal-menu {
    box-shadow: none; } }
